import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const Analytics = () => (
  <StaticQuery
    query={graphql`
      query {
        screen_athlete: file(
          sourceInstanceName: { eq: "screen" }
          name: { eq: "athlete" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        screen_coach: file(
          sourceInstanceName: { eq: "screen" }
          name: { eq: "coach" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="analytics">
        <Container>
          <Grid>
            <div>
              <h2>Athlete App</h2>
              <p>
                <ul>
                  <li>
                    View and compare your performance.
                  </li>
                  <li>
                    Record best sessions and training analytics.
                  </li>
                  <li>
                    Textbook biomechanics for insights into form and technique.
                  </li>
                  <li>
                    View real time 3D playback of your posture.
                  </li>
                </ul>
              </p>
            </div>
            <Art>
              <Img fluid={data.screen_athlete.childImageSharp.fluid} />
            </Art>
          </Grid>
          <Grid inverse>
            <Art>
              <Img fluid={data.screen_coach.childImageSharp.fluid} />
            </Art>
            <div>
              <h2>Coach App</h2>
              <p>
                <ul>
                  <li>
                    Real time data analytics for the coach to view athlete’s form and performance.
                  </li>
                  <li>
                    Live mode and session summary.
                  </li>
                  <li>
                    Track and record multiple players at once.
                  </li>
                </ul>
              </p>
            </div>
          </Grid>
        </Container>
      </Section>
    )}
  />
);

const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 2fr;
  grid-gap: 40px;
  text-align: left;
  align-items: center;
  justify-items: center;
  margin: 24px 0;

  ${props =>
    props.inverse &&
    `
    grid-template-columns: 2fr 3fr;
  `}

  h2 {
    margin-bottom: 16px;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }

    ${props =>
      props.inverse &&
      `
        ${Art} {
          order: 2;
        }
    `}
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 380px;
  width: 100%;
`;

export default Analytics;
