import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Container } from '@components/global';
import ExternalLink from '@common/ExternalLink';

import PhoneIcon from '@media/icon/phone.svg';
import InstagramIcon from '@media/icon/instagram.svg';
import FacebookIcon from '@media/icon/facebook.svg';
import YoutubeIcon from '@media/icon/youtube.svg';

const SOCIAL = [
  {
    icon: PhoneIcon,
    link: 'tel:+918368623613',
  },
  {
    icon: InstagramIcon,
    link: 'https://instagram.com/tweeklabs/',
  },
  {
    icon: FacebookIcon,
    link: 'https://facebook.com/tweek.labs.in/',
  },
  {
    icon: YoutubeIcon,
    link: 'https://www.youtube.com/channel/UCkwj2Ai7mpuKP7QjHzfsETg',
  },
];

const Footer = () => (
  <StaticQuery
    query={graphql`
      query {
        feature_win: file(
          sourceInstanceName: { eq: "feature" }
          name: { eq: "win" }
        ) {
          childImageSharp {
            fluid(maxWidth: 960) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <React.Fragment>
        <Art>
          <Img
            fluid={data.feature_win.childImageSharp.fluid}
            style={{ width: 480, maxWidth: '100%', marginBottom: -16 }}
          />
        </Art>
        <FooterWrapper>
          <StyledContainer>
            <Copyright>
              <h2>Tweek Labs</h2>
            </Copyright>
            <SocialIcons>
              {SOCIAL.map(({ icon, link }) => (
                <ExternalLink key={link} href={link}>
                  <img src={icon} alt="link" />
                </ExternalLink>
              ))}
            </SocialIcons>
          </StyledContainer>
        </FooterWrapper>
      </React.Fragment>
    )}
  />
);

const SocialIcons = styled.div`
  display: flex;

  img {
    margin: 0 8px;
    width: 24px;
    height: 24px;
  }

  @media (max-width: ${props => props.theme.screen.sm}) {
    margin-top: 40px;
  }
`;

const FooterWrapper = styled.footer`
  background-color: ${props => props.theme.color.primary};
  padding: 32px 0;
`;

const Copyright = styled.div`
  font-family: ${props => props.theme.font.secondary};
  ${props => props.theme.font_size.small};
  color: ${props => props.theme.color.black.regular};

  a {
    text-decoration: none;
    color: inherit;
  }
`;

const Art = styled.figure`
  display: flex;
  justify-content: center;
  margin: 0;
  margin-top: 48px;
`;

const StyledContainer = styled(Container)`
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: ${props => props.theme.screen.sm}) {
    flex-direction: column;
    text-align: center;
  }
`;

export default Footer;
