import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const Features = () => (
  <StaticQuery
    query={graphql`
      query {
        feature_record: file(
          sourceInstanceName: { eq: "feature" }
          name: { eq: "record" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        feature_ideas: file(
          sourceInstanceName: { eq: "feature" }
          name: { eq: "ideas" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        feature_win: file(
          sourceInstanceName: { eq: "feature" }
          name: { eq: "win" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }        
      }
    `}
    render={data => (
      <Section id="features">
        <Container>
          <Grid>
            <div>
              <h2>Record Yourself</h2>
              <p>
              Get yourself recorded during your practice sessions by simply wearing our sensors.
              </p>
            </div>
            <Art>
              <Img fluid={data.feature_record.childImageSharp.fluid} />
            </Art>
          </Grid>
          <Grid inverse>
            <Art>
              <Img fluid={data.feature_ideas.childImageSharp.fluid} />
            </Art>
            <div>
              <h2>Know Yourself</h2>
              <p>
                Learn about the flaws and strengths in your technique through biomechanical data analysis.
              </p>
            </div>
          </Grid>
          <Grid>
            <div>
              <h2>Improve Yourself</h2>
              <p>
                Track your improvement and progress over time and focus on your weak points to get better.
              </p>
            </div>
            <Art>
              <Img fluid={data.feature_win.childImageSharp.fluid} />
            </Art>
          </Grid>
        </Container>
      </Section>
    )}
  />
);

const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 2fr;
  grid-gap: 40px;
  text-align: right;
  align-items: center;
  justify-items: center;
  margin: 24px 0;

  ${props =>
    props.inverse &&
    `
    text-align: left;
    grid-template-columns: 2fr 3fr;
  `}

  h2 {
    margin-bottom: 16px;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }

    ${props =>
      props.inverse &&
      `
        ${Art} {
          order: 2;
        }
    `}
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 380px;
  width: 100%;
`;

export default Features;
