import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';

const Product = () => (
  <StaticQuery
    query={graphql`
      query {
        product_wearbit: file(
          sourceInstanceName: { eq: "product" }
          name: { eq: "wearbit" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        product_wear: file(
          sourceInstanceName: { eq: "product" }
          name: { eq: "wear" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        product_bits: file(
          sourceInstanceName: { eq: "product" }
          name: { eq: "bits" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="product" accent>
        <ColumnContainer>
          <Art>
            <Img fluid={data.product_wearbit.childImageSharp.fluid} />
          </Art>
          <Column>
            <div>
              <h2>Tweek Bits</h2>
              <p>MOTION SENSING MODULES</p>
              <Grid>
                <Art>
                  <Img fluid={data.product_bits.childImageSharp.fluid} />
                </Art>
                <p>
                  <ul>
                    <li>
                      Hassle free setup and capture in outdoor environment.
                    </li>
                    <li>
                      Battery life of upto 3 hours.
                    </li>
                    <li>
                      RGB LED for error detection.
                    </li>
                  </ul>
                </p>
              </Grid>
            </div>
            <div>
              <h2>Tweek Wear</h2>
              <p>PLAYER'S PERSONAL SUIT</p>
              <Grid>
                <Art>
                  <Img fluid={data.product_wear.childImageSharp.fluid} />
                </Art>
                <p>
                  <ul>
                    <li>
                      Sockets for Tweek Bits to fit in for highest accuracy.
                    </li>
                    <li>
                      Thermal and moisture regulation.
                    </li>
                    <li>
                      Injury prevention by keeping the muscles compressed.
                    </li>
                  </ul>
                </p>
              </Grid>
            </div>
          </Column>
        </ColumnContainer>
      </Section>
    )}
  />
);

const ColumnContainer = styled(Container)`
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 40px;
  text-align: left;
  align-items: center;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Column = styled.div`
  
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  text-align: left;
  align-items: center;
  justify-items: center;
  margin: 24px 0;

  h2 {
    margin-bottom: 16px;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 380px;
  width: 100%;
`;

export default Product;
